/**
 * 封装请求
 */
import axios from 'axios'
import { deepClone } from '@/utils/copyUtil'
import { Message, MessageBox } from 'element-ui'
import store from '@/store'
import * as crypto from '@/utils/cryptojs'
/* eslint-disable prefer-promise-reject-errors */
const instance = axios.create({
  baseURL: process.env.VUE_APP_REQUEST_BASE_API,
  withCredentials: false,
  timeout: 90000,
  headers: {
    Accept: '*',
    'Content-Type': 'application/json',
  },
})
// 拦截请求
instance.interceptors.request.use(
  function (req) {
    // 防止参数被篡改，需要对参数加密
    let key = [
      '75',
      '25',
      '37',
      '74',
      '6e',
      '6c',
      '70',
      '35',
      '4e',
      '69',
      '30',
      '70',
      '76',
      '4a',
      '54',
      '7a',
    ]
    key = crypto.hexToStr(key)
    let param
    if (req.method === 'get') {
      param = req.url.split('?')[1]
      if (!param) {
        // 可能不在url里，在params参数里
        const sdata = []
        for (const attr in req.params) {
          sdata.push(`${attr}=${req.params[attr]}`)
        }
        param = sdata.join('&')
        if (param) {
          req.params = {} // 将参数放到url上，不然特殊字符会被处理，后台不好解析
          req.url += '?'.concat(param)
        }
      }
    } else if (req.method === 'post') {
      param = JSON.stringify(req.data)
    }
    if (param) {
      let sign = ['73', '69', '67', '6e']
      sign = crypto.hexToStr(sign)
      const encode = crypto.Encrypt(param, key)
      req.headers.common[sign] =
        encode.length > 30 ? encode.substring(encode.length - 30, encode.length) : encode // 大于30位取最后30位
    }
    return req
  },
  function (err) {
    Message({ message: err, type: 'error' })
    return Promise.reject(err)
  }
)
// 拦截返回
instance.interceptors.response.use(
  function (res) {
    if (res.data.code === '0') {
      return res.data.data
    } else if (
      res.headers &&
      res.headers['content-type'] === 'application/vnd.ms-excel;charset=gb2312'
    ) {
      return Promise.resolve(res.data)
    } else if (res.request.responseURL.indexOf('/verifyCode/getVerifyCode') != -1) {
      return res.data
    } else if (res.data.code === 'CMP_EXPIRED' || res.data.code === 'CMP_NEED_AUTH') {
      return res.data
    } else {
      Message({ message: res.data.message, type: 'error' })
      return Promise.reject(res.data.message)
    }
  },
  function (err) {
    if (err.response) {
      // eslint-disable-next-line prefer-regex-literals
      const reg = new RegExp('[\\u4E00-\\u9FFF]+', 'g')
      let message = ''
      // 如果不包含中文基本是堆栈信息，统一封装成服务异常
      if (!reg.test(err.response.data.message)) {
        message = '服务异常,请稍后重试'
      } else {
        message = err.response.data.message
      }
      switch (err.response.status) {
        case 401: // 返回 401 清除token信息并跳转到登录页面
          {
            const msg = err.response.data.resultMsg || '会话已过期，请重新登录!'
            MessageBox.alert(msg, '提示信息', {
              confirmButtonText: '确定',
              type: 'warning',
              showClose: false,
              callback: (_) => {
                store.dispatch('user/logout')
              },
            })
          }
          break
        case 403:
          Message({
            message: err.response.data,
            type: 'error',
            dangerouslyUseHTMLString: true,
          })
          return Promise.reject(err)
        case 500:
        case 504:
          Message({
            message: message,
            type: 'error',
            dangerouslyUseHTMLString: true,
          })
          return Promise.reject(err)
        default:
          Message({
            message: message,
            type: 'error',
            dangerouslyUseHTMLString: true,
          })
          return Promise.reject(err)
        // break
      }
    } else {
      Message({ message: err, type: 'error' })
      return Promise.reject(err)
    }
  }
)

export default async (url = '', query = {}, option = {}) => {
  const params = option.method === 'upload' ? query : deepClone(query)
  if (!url) {
    return Promise.reject(`params 'url' not exist！`)
  }
  const method = option.method || 'post'
  switch (method) {
    case 'get':
      return instance.get(url, {
        params: params,
      })
    case 'image':
      return instance.get(url, {
        params: params,
        responseType: 'blob',
      })
    case 'upload': {
      const formData = new FormData()
      for (const key in params) {
        formData.append(key, params[key])
      }
      return instance.post(url, formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      })
    }
    case 'post':
      return instance.post(url, params, option)
    case 'put':
      return instance.put(url, params, option)
    case 'patch':
      return instance.patch(url, params, option)
    case 'delete':
      return instance.delete(url, {
        params: params,
      })
    default:
      return Promise.reject(`unknown request method '${method}'`)
  }
}
