<template>
  <div class="headers">
    <!-- 左边 -->
    <div class="headers-left">
      <div class="logo-container">
        <svg-icon icon="logo" class="vector-style"></svg-icon>
      </div>
      <!-- <div class="headers-left-seperator"></div> -->
      <span class="headers-left-title">{{ $t('login.headerTitle') }}</span>
    </div>
    <!-- 右边 -->
    <div class="headers-right">
      <div class="headers-right-company" @click="toCorpInfo">
        <svg-icon icon="company" class="headers-right-company-icon"></svg-icon>
        {{ user.custName || '' }}
      </div>
      <div class="headers-left-seperator"></div>
      <LangSwitch :is-login="true" />
      <div class="headers-left-seperator"></div>
      <div class="headers-right-download" @click="toDownload">
        <svg-icon icon="download" class="right-icon"></svg-icon>
        {{ $t('common.downloadMag') }}
      </div>
      <div class="headers-left-seperator"></div>
      <div class="headers-right-block" @click="goHome">
        <el-avatar shape="circle" size="small" icon="el-icon-user-solid"></el-avatar>
      </div>
      <el-dropdown size="medium" trigger="click" @command="handleCommand">
        <span class="el-dropdown-link">
          <span>
            {{ user.userName || '' }}
          </span>
          <i class="el-icon-arrow-down el-icon--right"></i>
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item divided command="basicInfo">{{
            $t('common.basicInfo')
          }}</el-dropdown-item>
          <el-dropdown-item divided command="quit">{{
            $t('common.logout')
          }}</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import LangSwitch from '@/components/LangSwitch/index.vue'

// 面包屑
// import Breadcrumb from '../Breadcrumb/Breadcrumb.vue'
export default {
  components: {
    LangSwitch,
  },
  data() {
    return {
      avatar: '',
      dropShow: false, // 是否现实通知栏
      setTime: null, // 定时器
      theme: false, // 主题 开关
      search: false, // 搜索开关
      searchValue: '',
      restaurants: [], // 所有路由
      dialogFormVisible: false, // 个人信息
      language: {},
      languageList: [
        {
          value: 'zh',
          title: '简体中文',
        },
        {
          value: 'en',
          title: 'English',
        },
      ],
    }
  },
  computed: {
    ...mapState('theme', ['showSidebar']),
    ...mapState('user', ['user']),
  },

  methods: {
    ...mapMutations('theme', ['SET_SHOW_SIDEBAR']),
    ...mapMutations('user', ['SET_USER']),
    // 自定义 切换 侧边栏 事件
    targetIcon() {
      this.SET_SHOW_SIDEBAR(!this.showSidebar)
    },
    handleCommand(command) {
      switch (command) {
        case 'quit':
          this.$store.dispatch('user/logout')
          break
        case 'basicInfo':
          this.$router.push({ name: 'basicInfo' })
          break
      }
    },
    toDownload() {
      this.$router.push('/common/download')
    },
    toCorpInfo() {
      this.$router.push('/common/corpInfo')
    },
    goHome() {},
  },
}
</script>
