import fetch from '@/request/fetch'

export default {
  custFee: {
    // 导出
    batchExportSimFee(params = {}) {
      return fetch('/custFee/batchExportSimFee', params, { method: 'get' })
    },
    // 查询企业账单列表
    queryCustFeePage(params = {}) {
      return fetch('/custFee/queryCustFeePage', params, { method: 'post' })
    },
    // 账单信息和费用详情
    queryByCustFeeCode(params = {}) {
      return fetch('/custFee/queryByCustFeeCode', params, { method: 'get' })
    },
    // SIM卡账单-分页查询
    querySimFeePage(params = {}) {
      return fetch('/custFee/querySimFeePage', params, { method: 'post' })
    },
  },
}
