<template>
  <el-select
    v-model="selectVal"
    :disabled="disabled"
    :clearable="clearable"
    :filterable="filterable"
    :multiple="multiple"
    :size="size"
    :loading="loading"
    :collapse-tags="collapseTags"
    :multiple-limit="multipleLimit"
    :placeholder="placeholder"
    @change="selectChange"
    @clear="clearAll"
  >
    <template v-if="isGroup">
      <el-option-group
        v-for="group in options"
        :key="group[groupLabelKey]"
        :label="group[groupLabelKey]"
      >
        <el-option
          v-for="item in group[optionsLabel]"
          :key="item[rowKey] || item[valueKey]"
          :label="typeof labelKey === 'string' ? item[labelKey] : labelKey(item)"
          :value="item[valueKey]"
        ></el-option>
      </el-option-group>
    </template>
    <template v-else>
      <el-option
        v-for="item in options"
        :key="strkv ? item : item[rowKey] || item[valueKey]"
        :label="
          strkv ? item : typeof labelKey === 'string' ? item[labelKey] : labelKey(item)
        "
        :value="strkv ? item : item[valueKey]"
        :disabled="strkv ? false : item.disabled"
      ></el-option>
    </template>
  </el-select>
</template>
<script>
export default {
  name: 'CtSelect',
  props: {
    placeholder: {
      type: String,
      default: () => '请选择',
    },
    multipleLimit: {
      type: Number,
      default: 0,
    },
    disabled: {
      type: Boolean,
      default: () => false,
    },
    loading: {
      type: Boolean,
      default: () => false,
    },
    labelKey: {
      type: [String, Function],
      default: () => 'label',
    },
    valueKey: {
      type: String,
      default: () => 'value',
    },
    rowKey: {
      type: String,
      default: () => '',
    },
    options: {
      type: Array,
      default: () => [],
    },
    filterable: {
      type: Boolean,
      default: () => false,
    },
    clearable: {
      type: Boolean,
      default: () => true,
    },
    value: {
      type: [String, Array, Number],
      default: () => '',
    },
    multiple: {
      type: Boolean,
      default: () => false,
    },
    collapseTags: {
      type: Boolean,
      default: () => false,
    },
    // list 单项是string，对应的每项key和value都是item对应的值，设置该项为true
    strkv: {
      type: Boolean,
      default: () => false,
    },
    optionsLabel: {
      type: String,
      default: () => 'options',
    },
    groupLabelKey: {
      type: String,
      default: () => 'label',
    },
    isGroup: {
      type: Boolean,
      default: () => false,
    },
    size: {
      type: String,
      default: () => '',
    },
  },
  data() {
    return {
      selectVal: '',
    }
  },
  watch: {
    value: {
      handler(newVal, oldVal) {
        if (newVal !== oldVal) {
          this.selectVal = newVal
        }
      },
      immediate: true,
    },
  },
  methods: {
    clearAll(val) {
      this.$emit('clear', val)
    },
    selectChange(val) {
      if (this.multiple) {
        const isSelectAll = val.length === this.options.length
        this.$emit('change', val, isSelectAll)
        this.$emit('input', val)
      } else {
        this.$emit('change', val)
        this.$emit('input', val)
      }
    },

    // 全选/全不选
    selectAll(isSelectAll) {
      if (isSelectAll) {
        this.selectVal =
          this.options.map((o) => (this.strkv ? o : o[this.valueKey])) || []
        this.$emit('change', this.selectVal)
        this.$emit('input', this.selectVal)
      } else {
        this.selectVal = []
        this.$emit('change', this.selectVal)
        this.$emit('input', this.selectVal)
      }
    },
  },
}
</script>
