<template>
  <div class="sidebar-bottom">
    <div class="sidebar-bottom-box" @click="targetIcon">
      <i :class="['el-icon-s-fold', showSidebar ? '' : 'sidebar-bottom-active']"></i>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
export default {
  name: 'LeftCallapse',
  computed: {
    ...mapState('theme', ['showSidebar']),
  },
  methods: {
    ...mapMutations('theme', ['SET_SHOW_SIDEBAR']),
    targetIcon() {
      this.SET_SHOW_SIDEBAR(!this.showSidebar)
    },
  },
}
</script>

<style lang="scss" scoped></style>
