<template>
  <ct-select
    v-model="billCycle"
    :placeholder="selectPh('dataUsage.billCycle')"
    :options="billCycleOptions"
    :clearable="false"
    v-bind="$attrs"
  >
  </ct-select>
</template>

<script>
import langMixin from '@/mixins/langMixin'
import CtSelect from '../CtSelect'
export default {
  name: 'BillCycleSelect',
  components: {
    CtSelect,
  },
  mixins: [langMixin],
  props: {
    value: {
      type: [String, Number],
      default: '',
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      billCycle: '',
    }
  },
  computed: {
    billCycleOptions() {
      const list = []
      // 定义当前日期
      const currentDate = new Date()
      // 循环六次以获取过去六个月的数据
      for (let i = 0; i < 7; i++) {
        // 创建一个新的日期对象，表示六个月前的某个月
        const pastDate = new Date(
          currentDate.getFullYear(),
          currentDate.getMonth() - i,
          1
        )
        // 获取这个月的年份和月份（注意：getMonth() 返回的是从0开始的月份）
        const year = pastDate.getFullYear()
        const month = ('0' + (pastDate.getMonth() + 1)).slice(-2) // 确保月份为两位数

        // 格式化输出，例如 "202306"
        const value = `${year}${month}`
        const label = `${year}-${month}`
        // 将数据添加到结果数组中
        list.push({
          label,
          value,
        })
      }
      // 返回结果数组
      return list
    },
  },
  watch: {
    value: {
      handler(newVal, oldVal) {
        if (newVal !== oldVal) {
          this.billCycle = newVal
        }
      },
      immediate: true,
    },
    billCycle(val) {
      this.$emit('input', val)
    },
  },
  mounted() {
    this.billCycle = this.billCycleOptions[0].value || ''
  },
  methods: {},
}
</script>

<style scoped></style>
