<script>
export default {
  name: 'MenuItem',
  functional: true,
  props: {
    icon: {
      type: String,
      default: '',
    },
    title: {
      type: String,
      default: '',
    },
  },
  render(h, context) {
    const { icon, title } = context.props
    const vnodes = []
    if (icon) {
      vnodes.push(
        <span class="menu--collapes-icon">
          <svg-icon icon={icon} class-name="menu_svg" class="menu-icon" />
        </span>
      )
    }
    if (title) {
      vnodes.push(
        <span slot="title" class="tel-menu-title">
          {title}
        </span>
      )
    }

    return vnodes
  },
}
</script>
<style>
.tel-menu-title {
  margin-left: 10px;
}
.menu_svg {
  /* margin-left: 5px;
  margin-right: 10px; */
  width: 14px !important;
  height: 14px !important;
  margin-bottom: 1px;
}
.menu-icon {
  color: #333;
}
</style>
