<template>
  <el-date-picker
    v-model="dateRange"
    type="datetimerange"
    :picker-options="pickerOptions"
    value-format="yyyy-MM-dd HH:mm:ss"
    range-separator="-"
    :start-placeholder="$t('common.beginTime')"
    :end-placeholder="$t('common.endTime')"
    @change="handleDateChange"
  />
</template>

<script>
export default {
  name: 'CtDatePicker',
  props: {
    // 父组件传入的 begin 和 end，作为初始化值
    begin: {
      type: String,
      default: '',
    },
    end: {
      type: String,
      default: '',
    },
    oneMonthRange: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      // 本地存储的日期范围，初始化为父组件传入的值
      dateRange: [this.begin, this.end],
      choiceDate0: '',
      pickerOptions: {
        onPick: ({ maxDate, minDate }) => {
          if (minDate) {
            this.choiceDate0 = minDate.getTime()
          } else {
            this.choiceDate0 = new Date()
          }
          if (maxDate) {
            this.choiceDate0 = ''
          }
        },
        disabledDate: (time) => {
          if (!this.oneMonthRange) {
            return
          }
          const choiceDateTime = new Date(this.choiceDate0).getTime()
          if (this.choiceDate0) {
            const diff = 30 * 24 * 3600 * 1000
            return (
              time.getTime() > choiceDateTime + diff ||
              time.getTime() < choiceDateTime - diff
            )
          }
          return false
        },
      },
    }
  },
  watch: {
    // 监听父组件的 begin 和 end 更新，重新赋值给本地 dateRange
    begin(newbegin) {
      !newbegin && (this.dateRange = ['', '']) // 如果 begin 为空，则清空 end
    },
    end(newend) {
      !newend && (this.dateRange = ['', '']) // 如果 end 为空，则清空 begin
    },
    // 监听 dateRange 更新，将新值传递给父组件
    dateRange(newRange) {
      let rangge = newRange
      if (!newRange) {
        rangge = ['', '']
      }
      this.$emit('update:begin', rangge[0]) // 更新父组件的 begin
      this.$emit('update:end', rangge[1]) // 更新父组件的 end
    },
  },
  methods: {
    handleDateChange() {
      // 处理日期选择变化时的逻辑
      console.log('日期范围变化：', this.dateRange)
    },
  },
}
</script>
