import Vue from 'vue'
import store from '@/store'

/**
 * 权限指令
 **/
const compPriv = Vue.directive('compPriv', {
  inserted: async function (el, binding) {
    // 获取按钮权限
    const compPrivs = await store.dispatch('user/queryMenuCompPriv')
    let flag = false
    if (compPrivs != null && compPrivs !== 'null') {
      compPrivs.forEach((item) => {
        if (item.privCode === binding.value.toString()) {
          flag = true
        }
      })
    }
    if (!flag) {
      el.parentNode.removeChild(el)
    }
  },
})

export default compPriv
