import fetch from '@/request/fetch'

export default {
  userMag: {
    // 获取业务类型下拉框的值和描述
    getBatchTaskAllBusiType(params = {}) {
      return fetch('/batch/getBatchTaskAllBusiType', params, { method: 'get' })
    },
    // 查询下载列表
    qryBatchList(params = {}) {
      return fetch('batch/qryBatchList', params, { method: 'post' })
    },
    // 修改插件
    updateMifiPlugin(params = {}) {
      return fetch('/plugin/updateMifiPlugin', params, { method: 'upload' })
    },
  },
}
