export default {
  methods: {
    inputPh(key) {
      const prefix = this.$i18n.locale === 'zh' ? '请输入' : 'Please Input '
      return prefix + this.$t(key)
    },
    selectPh(key) {
      const prefix = this.$i18n.locale === 'zh' ? '请选择' : 'Please Select '
      return prefix + this.$t(key)
    },
  },
}
