export function parseQueryString(url) {
  const queryString = url.split('?')[1] // 获取查询字符串
  const params = {} // 初始化一个空对象来存储参数

  if (queryString) {
    const pairs = queryString.split('&') // 将查询字符串分割成键值对
    for (const pair of pairs) {
      const [key, value] = pair.split('=') // 分割每个键值对
      params[key] = value ? decodeURIComponent(value) : '' // 解码并添加到对象中
    }
  }

  return params
}
