<template>
  <div class="show-more-button" @click="isShowMore = !isShowMore">
    <div>{{ isShowMore ? $t('common.showMoreBtn') : $t('common.showMoreBtn') }}</div>
    <!-- <img
      src="@/assets/image/show_more.png"
      :style="{ transform: isShowMore ? 'rotate(0)' : 'rotate(180deg)' }"
    /> -->
    <i :class="iconClass"></i>
  </div>
</template>

<script>
export default {
  name: 'ShowMoreButton',
  props: {
    value: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      isShowMore: this.value,
    }
  },
  computed: {
    iconClass() {
      return this.isShowMore ? 'el-icon-arrow-up' : 'el-icon-arrow-down'
    },
  },
  watch: {
    isShowMore() {
      this.$emit('input', this.isShowMore)
    },
  },
}
</script>

