<template>
  <div class="sidebar">
    <!-- <div class="sidebar-logo">
      <svg-icon icon="logo" class="vector-style"></svg-icon>
      {{ title }}
    </div> -->
    <div class="sidebar-box">
      <el-menu
        class="el-menu-vertical-demo"
        :collapse="!showSidebar"
        :class="showSidebar ? 'sidebar-menu' : ''"
        :default-active="activeMenu"
        background-color="transparent"
        :text-color="variables.menuText"
        :unique-opened="true"
        :active-text-color="variables.menuActiveText"
        :collapse-transition="true"
        mode="vertical"
      >
        <SidebarItem
          v-for="route in routes"
          :key="route.menuId"
          :item="route"
          :base-path="route.path"
        />
      </el-menu>
    </div>
    <LeftCallapse />
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex'
import SidebarItem from './SidebarItem.vue'
import LeftCallapse from './LeftCallapse.vue'

export default {
  components: { SidebarItem, LeftCallapse },
  data() {
    return {}
  },

  computed: {
    ...mapState('theme', ['showSidebar']),
    ...mapState('user', ['routes']),
    activeMenu() {
      const route = this.$route
      const { meta, path } = route
      // 默认激活项
      if (meta.activeMenu) {
        return meta.activeMenu
      }
      return path
    },
    variables() {
      return {
        menuText: '#333', // "#bfcbd9"
        menuActiveText: '#ffffff',
      }
    },
    title() {
      if (this.showSidebar) {
        return this.$t('login.headerTitle')
      }
      return ''
    },
  },
  mounted() {
    if (!this.showSidebar) {
      this.title = ''
    }
    this.getMenuPriv()
  },
  methods: {
    ...mapActions('user', ['getMenuPriv']),
  },
}
</script>
<style lang="scss" scoped>
.vector-style {
  display: inline-block;
  width: 40px;
  height: 28px;
  position: relative;
  top: 6px;
  color: #3274fa;
}
</style>
