<template>
  <div class="overview_container">
    <div class="label">{{ label }}</div>
    <el-tooltip
      effect="dark"
      :content="contentHover"
      placement="top"
      :disabled="isShowTooltip"
    >
      <span ref="name" class="value" @mouseover="onMouseOver()">{{ content }}</span>
    </el-tooltip>
  </div>
</template>

<script>
export default {
  name: 'CtTooltip',
  props: {
    label: {
      type: String,
      default: '',
    },
    content: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      isShowTooltip: true,
      contentHover: '',
    }
  },
  methods: {
    onMouseOver() {
      // debugger
      // 内容超出，显示文字提示内容
      const tag = this.$refs.name
      const parentWidth = tag.parentNode.offsetWidth // 获取元素父级可视宽度
      const contentWidth = tag.offsetWidth // 获取元素可视宽度
      this.isShowTooltip = contentWidth < parentWidth
      // 鼠标悬停后显示的内容
      this.contentHover = this.content
    },
  },
}
</script>

<style lang="scss" scoped>
.overview_container {
  font-weight: 500;
  font-size: 14px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  .label {
    color: #666;
    margin-bottom: 15px;
  }
  .value {
    color: #333333;
  }
}
</style>
