<template>
  <div
    class="em_table_box"
    :class="[
      autoHeight
        ? 'auto_height'
        : !(height || maxHeight)
        ? 'display_flex'
        : 'auto_height',
    ]"
  >
    <el-table
      ref="em_table_component"
      v-loading="loading"
      fit
      border
      :size="size"
      :cell-style="cellStyle"
      :header-cell-style="headerCellStyle"
      :header-cell-class-name="headerCellClassName"
      :height="autoHeight ? undefined : height || (maxHeight ? undefined : '100%')"
      :max-height="maxHeight"
      style="width: 100%"
      :data="data"
      :default-sort="defaultSort"
      @row-click="rowClick"
      @selection-change="handleSelectionChange"
      @sort-change="handleSortChange"
    >
      <el-table-column
        v-if="showSelect"
        type="selection"
        width="50"
        :selectable="selectable"
      >
      </el-table-column>
      <el-table-column
        v-if="showIndex"
        label="序号"
        width="50"
        :fixed="indexFixed"
        align="center"
        type="index"
        :index="indexMethod"
      ></el-table-column>
      <slot></slot>
    </el-table>
    <el-pagination
      v-if="pagination"
      background
      class="table_pagination"
      :current-page="pagination.pageIndex"
      :total="total"
      :page-sizes="pageSizes"
      :page-size="pagination.pageSize"
      :small="small"
      align="right"
      :layout="layout"
      :pager-count="pagerCount"
      @size-change="sizeChange"
      @current-change="pageChange"
    >
    </el-pagination>
  </div>
</template>
<script>
export default {
  name: 'CtTable',
  props: {
    autoHeight: {
      type: Boolean,
      default: () => false,
    },
    height: {
      type: [String, Number],
      default: () => undefined,
    },
    maxHeight: {
      type: [String, Number],
      default: () => undefined,
    },
    loading: {
      type: Boolean,
      default: () => false,
    },
    showSelect: {
      type: Boolean,
      default: () => false,
    },
    // 显示序号
    showIndex: {
      type: Boolean,
      default: () => false,
    },
    // 序号固定在左侧
    indexFixed: {
      type: Boolean,
      default: () => false,
    },
    total: {
      type: [Number, String],
      default: () => 0,
    },
    pagination: {
      type: [Boolean, Object],
      default: () => false,
    },
    pageSizes: {
      type: Array,
      default: () => [10, 20, 50, 100],
    },
    data: {
      type: Array,
      default: () => [],
    },
    selectable: {
      type: Function,
      default: () => () => true,
    },
    cellStyle: {
      type: [Function, Object],
      default: () => {},
    },
    defaultSort: {
      type: Object,
      default: () => {},
    },
    headerCellStyle: {
      type: [Function, Object],
      default: () => {},
    },
    headerCellClassName: {
      type: Function,
      default: () => () => {},
    },
    small: {
      type: Boolean,
      default: () => false,
    },
    layout: {
      type: String,
      default: () => 'total, sizes, prev, pager, next, jumper',
    },
    pagerCount: {
      type: Number,
      default: () => 7,
    },
    size: {
      type: String,
      default: () => '',
    },
  },
  methods: {
    indexMethod(index) {
      return !this.pagination
        ? index + 1
        : index + 1 + this.pagination.pageSize * (this.pagination.pageIndex - 1)
    },
    sizeChange(pageSize) {
      this.$emit('change', { pageIndex: this.pagination.pageIndex, pageSize: pageSize })
    },
    pageChange(pageNo) {
      this.$emit('change', { pageIndex: pageNo, pageSize: this.pagination.pageSize })
    },
    handleSelectionChange(val) {
      this.$emit('selection-change', val)
    },
    handleSortChange(val) {
      this.$emit('sort-change', val)
    },
    rowClick(val) {
      this.$emit('row-click', val)
    },
    toggleRowSelection(row) {
      this.$refs.em_table_component.toggleRowSelection(row)
    },
    doLayout() {
      this.$refs.em_table_component.doLayout()
    },
    sort(prop, order) {
      console.log(prop, order)
      this.$refs.em_table_component.sort(prop, order)
    },
  },
}
</script>
<style lang="scss" scoped>
.em_table_box {
  &.display_flex {
    flex: 1;
    height: 0;
    display: flex;
    flex-direction: column;
  }
  &.auto_height {
    height: auto;
  }
  .table_pagination {
    margin-top: 20px;
  }
}
</style>
