import CtTable from './CtTable'
import CtSelect from './CtSelect'
import BillCycleSelect from './BillCycleSelect'
import CtSectionTitle from './CtSectionTitle'
import CtRemoteSelect from './CtRemoteSelect'
import CtAsyncSelect from './CtAsyncSelect'
import CtTooltip from '@/components/CtTooltip'
import ShowMoreButton from './ShowMoreButton'
import PageTop from './PageTop'
import CtDatePicker from './CtDatePicker'

const components = [
  CtTable,
  CtSelect,
  CtRemoteSelect,
  CtAsyncSelect,
  CtSectionTitle,
  CtTooltip,
  ShowMoreButton,
  BillCycleSelect,
  PageTop,
  CtDatePicker,
]
const install = (Vue) => {
  components.forEach((comp) => {
    Vue.component(comp.name, comp)
  })
}

export default {
  install,
}
