<template>
  <el-select
    v-model="selectVal"
    :disabled="disabled"
    filterable
    remote
    reserve-keyword
    placeholder="请选择"
    :remote-method="remoteMethod"
    :loading="loading"
    @change="selectChange"
  >
    <el-option
      v-for="item in selectList"
      :key="strkv ? item : item[rowKey] || item[valueKey]"
      :label="strkv ? item : item[labelKey]"
      :value="strkv ? item : item[valueKey]"
    ></el-option>
  </el-select>
</template>
<script>
import fetch from '@/request/fetch'
export default {
  name: 'CtRemoteSelect',
  props: {
    disabled: {
      type: Boolean,
      default: false,
    },
    manual: {
      type: Boolean,
      default: false,
    },
    url: {
      type: String,
      default: () => '',
    },
    param: {
      type: Object,
      default: () => ({}),
    },
    method: {
      type: String,
      default: () => 'post',
    },
    labelKey: {
      type: String,
      default: () => 'label',
    },
    valueKey: {
      type: String,
      default: () => 'value',
    },
    queryKey: {
      type: String,
      default: () => 'queryKey',
    },
    filterable: {
      type: Boolean,
      default: () => false,
    },
    value: {
      type: [String, Array, Number],
      default: () => '',
    },
    multiple: {
      type: Boolean,
      default: () => false,
    },
    collapseTags: {
      type: Boolean,
      default: () => false,
    },
    // list 单项是string，对应的每项key和value都是item对应的值，设置该项为true
    strkv: {
      type: Boolean,
      default: () => false,
    },
    rowKey: {
      type: String,
      default: () => '',
    },
  },
  data() {
    return {
      selectList: [],
      selectVal: '',
      loading: false,
    }
  },
  watch: {
    value: {
      handler(newVal) {
        this.selectVal = newVal || ''
      },
      immediate: true,
    },
  },
  mounted() {
    // if (!this.manual) {
    //   this.init()
    // }
    this.remoteMethod()
  },
  methods: {
    async remoteMethod(query) {
      this.loading = true
      const param = { [this.queryKey]: query }
      const res = await fetch(this.url, param, { method: this.method })
      this.loading = false
      this.selectList = res
    },
    reset() {
      this.selectList = []
    },
    selectChange(val) {
      this.$emit('input', val)
      this.$emit('change', val)
    },
  },
}
</script>
