import fetch from '@/request/fetch'

const baseUrl = '/sys/user/out' // 自定义请求路径
export default {
  login: {
    // 判断错误验证码
    verifyAndLoginUser(params = {}) {
      return fetch(`/verifyAndLoginUser`, params, { method: 'get' })
    },
    // 验证码倒计时
    getResendSmsTime(params = {}) {
      return fetch(`/sys/user/smsVerify/getResendSmsTime`, params, { method: 'get' })
    },
    // 重置密码
    resetPassword(params = {}) {
      return fetch(`/sys/user/out/resetPassword`, params, { method: 'post' })
    },
    // 获取短信验证码
    sendReSetPassSmsCode(params = {}) {
      return fetch(`/sys/user/smsVerify/sendReSetPassSmsCode`, params, { method: 'get' })
    },
    // 获取二次验证码
    verifyUser(params = {}) {
      return fetch(`/verifyAndLoginUser`, params, { method: 'get' })
    },
    // 获取图片验证码
    getVerifyCode(params = {}) {
      return fetch(`/verifyCode/getVerifyCode`, params, { method: 'image' })
    },
    // 获取图片验证码序列
    getVerifyId(params = {}) {
      return fetch(`/verifyCode/getVerifyId`, params, { method: 'get' })
    },
    // 图片验证码开关
    getParamValueByCust(params = {}) {
      return fetch(`/param/getParamValueByCust`, params, { method: 'get' })
    },
    sendLoginSmsCode(params) {
      return fetch(`/sys/user/smsVerify/sendWebLoginSmsCode`, params, { method: 'get' })
    },
    // getCurrent() {
    //   return fetch(`${baseUrl}/current?t=` + Math.random(), {}, { method: 'get' })
    // },
    getCurrent() {
      return fetch('/user/current', {}, { method: 'get' })
    },
    getSrc(userCode) {
      return fetch(`${baseUrl}/src?userCode=` + userCode, {}, { method: 'get' })
    },
  },
}
