<template>
  <el-dropdown trigger="click" @command="changeLanguage">
    <div class="lang-container">
      <svg-icon :icon="`lang_earth`" class="icon"></svg-icon>
      {{ language.title }}<i class="el-icon-arrow-down el-icon--right"></i>
    </div>
    <el-dropdown-menu slot="dropdown">
      <el-dropdown-item
        v-for="(item, index) in languageList"
        :key="index"
        :command="index"
      >
        {{ item.title }}
      </el-dropdown-item>
    </el-dropdown-menu>
  </el-dropdown>
</template>

<script>
import fetch from '@/request/fetch'
export default {
  props: {
    isLogin: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      language: {},
      languageList: [
        {
          value: 'zh',
          title: '中文',
        },
        {
          value: 'en',
          title: 'English',
        },
      ],
    }
  },
  mounted() {
    this.setLanguage()
  },
  methods: {
    setLanguage() {
      const index = this.$i18n.locale === 'zh' ? 0 : 1
      this.language = this.languageList[index]
      this.localeSwitch()
    },
    changeLanguage(index) {
      this.language = this.languageList[index]
      const value = this.language.value
      if (value === this.$i18n.locale) {
        return
      }
      this.$i18n.locale = value
      localStorage.setItem('language', value)
      this.localeSwitch()
      // 刷新页面，产品觉得OK
      location.reload()
    },
    localeSwitch() {
      // 修改title
      document.title = this.$t('login.headerTitle')
      if (!this.isLogin) {
        return
      }
      fetch('/locale/switch', { language: this.$i18n.locale }, { method: 'get' })
    },
  },
}
</script>

