// 路由的全局守卫
// 引入nprogress 进度条插件
import NProgress from 'nprogress'
// 权限控制逻辑
import router from './index'
import store from '../store/index'
import loginApi from '@/request/modules/login'
import { MessageBox } from 'element-ui'
import fetch from '@/request/fetch'

const whiteList = ['/login', '/forgetPassword'] // 排除的路径

router.beforeEach(async (to, from, next) => {
  // 回到顶部
  if (document.getElementsByClassName('main-container-views')[0]) {
    document.getElementsByClassName('main-container-views')[0].scrollTop = 0
  }
  const roleCode = store._modules.root.state.user.user.roleCode

  // 判断操作权限
  const userFunc = async (res) => {
    if (to.path === '/') {
      const routes = await store.dispatch('user/getMenuPriv')
      const rediret = routes[0]?.children[0]?.path
      next(rediret)
    } else if (to.path === '/home' || to.path === '/forgetPassword') {
      next()
    } else if (to.path === '/login') {
      const ticket = to.query.ticket
      if (!ticket) {
        next()
      } else {
        const ticket = to.query.ticket
        if (!ticket) {
          next()
        } else {
          fetch(
            '/checkTicket',
            { ticket },
            {
              method: 'get',
            }
          )
            .then(() => next('/'))
            .catch(() => next())
        }
      }
    } else {
      const desPrivCode = to.meta.privCode
      if (!desPrivCode) {
        next()
      } else if (
        res.find((comp) => {
          return comp.privCode === desPrivCode.toString()
        })
      ) {
        next()
      } else {
        MessageBox.alert('没有此页面的操作权限!', '提示', {
          confirmButtonText: '确定',
          type: 'warning',
          showClose: false,
          callback: async (_) => {
            // const routes = await store.dispatch('user/getMenuPriv')
            // const rediret = routes[0]?.children[0]?.path
            // next(rediret)
          },
        })
      }
    }
  }
  // 获取菜单按钮权限
  const getComPriv = async (roleCode) => {
    const compPrivs = store.state.user.compPrivs
    if (compPrivs[0]?.roleCode !== roleCode) {
      store.commit('user/SET_COMPPRIVS', [])
    }
    const res = await store.dispatch('user/queryMenuCompPriv')
    userFunc(res)
  }
  // 有code信息 表示已经登陆
  if (roleCode) {
    // 简单配置  进度条
    NProgress.inc(0.2)
    NProgress.configure({ easing: 'ease', speed: 500, showSpinner: false })
    // 进度条
    NProgress.start()
    getComPriv(roleCode)
  } else {
    if (whiteList.indexOf(to.path) !== -1) {
      if (to.path === '/login') {
        const ticket = to.query.ticket
        if (!ticket) {
          next()
        } else {
          fetch(
            '/checkTicket',
            { ticket },
            {
              method: 'get',
            }
          )
            .then(() => next('/'))
            .catch(() => next())
        }
      } else {
        next()
      }
    } else {
      // next()
      loginApi.login.getCurrent().then(async (data) => {
        if (data) {
          getComPriv(data.roleCode)
          // getMenuPriv()
          store.commit('user/SET_USER', data)
        } else {
          next('/login')
          // MessageBox.alert('会话已过期, 请重新登录!', '提示', {
          //   confirmButtonText: '确定',
          //   type: 'warning',
          //   showClose: false,
          //   callback: (_) => {
          //     // window.location.href = '/login'
          //     next('/login')
          //   },
          // })
        }
      })
    }
  }
})

router.afterEach((path) => {
  // document.title = path.meta.title
  NProgress.done()
})
