import request from '@/request'
import router from '@/router'
import store from '..'
// import userMenuPriv from '@/mock/modules/sys/json'
// 默认加载本地用户信息
const state = {
  user: {},
  winHeight: 0,
  curMenuId: null,
  routes: [],
  compPrivs: [],
}
const mutations = {
  SET_USER: (state, user) => {
    user = user || {}
    state.user = user
  },
  SET_ROUTE: (state, routes) => {
    routes = routes || []
    state.routes = routes
  },
  SET_COMPPRIVS: (state, compPrivs) => {
    compPrivs = compPrivs || []
    state.compPrivs = compPrivs
  },
  SET_WINHEIGHT: (state, winHeight) => {
    winHeight = winHeight || 0
    state.winHeight = winHeight
  },
  SET_LOGOUT: (state) => {
    state.user = {}
    state.routes = []
    state.compPrivs = []
  },
}

const getters = {
  userRoleType(state) {
    // 登录客户角色类型
    return state.user.sysRole.roleType
  },
  userRoleCustId(state) {
    // 登录客户 custId
    return state.user.sysRole.custId
  },
  userId(state) {
    // 登录客户 custId
    return state.user.userId
  },
}

const actions = {
  // 获取左侧菜单
  async getMenuPriv({ commit, state }) {
    if (state.routes.length === 0) {
      const res = await request.sys.role.queryMenuPriv()
      // const res = userMenuPriv.data // 本地 mock数据
      function formatRouter(e) {
        const menus = e
        menus.forEach((item, index) => {
          item.path = item.menuUrl
          item.meta = {
            title: item.privName,
            enTitle: item.enPrivName,
            icon: item.logoUrl,
          }
          if (item.children?.length > 0) {
            item.children = formatRouter(item.children)
          }
        })
        return menus
      }
      formatRouter(res)
      commit('SET_ROUTE', res)
    }
    return state.routes
  },
  async queryMenuCompPriv({ commit, state }) {
    if (state.compPrivs.length === 0) {
      const res = await request.sys.role.queryMenuCompPriv()
      commit('SET_COMPPRIVS', res)
    }
    return state.compPrivs
  },
  async logout({ commit, dispatch }) {
    await request.sys.user.quitLogout()
    await store.dispatch('tagsView/delAllViews')
    localStorage.clear()
    sessionStorage.clear()
    commit('SET_LOGOUT')
    router.replace('/login')
  },
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions,
}
