<template>
  <div id="app">
    <router-view />
  </div>
</template>

<script>
import { mapMutations } from 'vuex'
export default {
  name: 'App',
  mounted() {
    this.SET_WINHEIGHT(
      window.innerHeight ||
        document.documentElement.clientHeight ||
        document.body.clientHeight
    )
    window.addEventListener('resize', () => {
      this.SET_WINHEIGHT(
        window.innerHeight ||
          document.documentElement.clientHeight ||
          document.body.clientHeight
      )
    })
  },
  methods: {
    ...mapMutations('user', ['SET_WINHEIGHT']),
  },
}
</script>

